import { browserDetect } from "./modules/browserDetect";
import path from "ramda/es/path";

const p = window.paramsFromServer;
const isLivestream = !!path(['livestream'], p);
const  isVOD = path(['video_data', 'type'], p) === 'livestream';
const element = document.createElement("div");
const config = p && p['playerConfig'] || {};

if (browserDetect.isSupporting(isLivestream, isVOD)) {
  element.id = "TCP-app";
  document.getElementById("TCP-wrapper").appendChild(element);
} else {
  const headerText = config['tclanguage'] && config.tclanguage['screenNotSupportedTitle'];
  const text = config['tclanguage'] && config.tclanguage['screenNotSupportedText'];
  if(headerText){
    document.getElementById("noSupportedHeader").innerHTML = headerText;
  }
  if(text){
    document.getElementById("noSupportedText").innerHTML = text;
  }
  document.getElementById("not-supported").style.display = "table-cell";
  document.getElementById("not-supported-wrapper").style.display = "table";
  console.error("Browser not supported");
}



